@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);

html{box-sizing:border-box;font-family:"Bebas Neue",cursive;font-size:20px;background-color:#62b16f}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{margin:0;padding:0;font-weight:normal}h1{color:#fff;text-shadow:3px 3px #44714a}ol,ul{list-style:none}img{max-width:100%;height:auto}button{font-family:"Bebas Neue",cursive;font-size:20px;border:none;box-shadow:none}input{font-family:"Bebas Neue",cursive;height:30px;font-size:28px;width:200px}.listed-player{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row;font-size:18px;text-shadow:none}.delete-button{width:30px;height:30px;margin-bottom:7px;margin-right:5px;background-color:#2b392a;color:#fff;border:none;box-shadow:none}#side-bar::-webkit-scrollbar{width:0;height:0}#side-bar{overflow-y:scroll;scrollbar-width:none;-ms-overflow-style:none;height:100%;width:300px;position:fixed;z-index:1;top:0;left:0;background-color:rgba(19,19,20,.5);overflow-x:hidden;padding-top:20px;padding-left:25px;box-shadow:0 10px 20px rgba(0,0,0,.19),0 6px 6px rgba(0,0,0,.23)}#quick-add-section{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding-right:10px}.quick-add-button{margin-right:3px;margin-bottom:3px}#mid-page-section{padding-left:330px;padding-top:30px}#add-player-section{margin-top:20px;margin-bottom:20px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}#add-button{margin-left:5px}.shuffle-button{background-color:#3b6241;color:#fff;width:200px;height:60px;margin-bottom:20px;box-shadow:0 1px 3px rgba(0,0,0,.12),0 1px 2px rgba(0,0,0,.24);font-size:30px;padding-top:5px}.shuffle-button:hover{box-shadow:0 3px 6px rgba(0,0,0,.16),0 3px 6px rgba(0,0,0,.23)}.action-button{background-color:#3b6241;color:#fff;width:90px;height:50px;margin-bottom:20px;margin-right:20px;box-shadow:0 1px 3px rgba(0,0,0,.12),0 1px 2px rgba(0,0,0,.24)}
